<template>
  <div>
    <!-- <indexHeader></indexHeader> -->
    <div id="legaltrade-box"  :class="skins=='days'?'white':'white balck'">
      <div class="buy-sell flex">
        <div class="buy-box bod_rc flex">
          <div class="title" :class="type=='sell'?'active':''" @click="changeClassify('sell')">{{$t('td.buy')}}</div>
          <div class="title" :class="type=='buy'?'active':''" @click="changeClassify('buy')">{{$t('td.sell')}}</div>
        </div>
        <div class="sell-box">
          <!-- <div class="application" v-if="applicationShow" @click="application">申请成为商家</div> -->
          <div class="record light_blue" @click="recordList()">{{$t('fat.orderLog')}}</div>
        </div>
      </div>
      <!-- 法币列表 -->
      <div class="ml20">
        <ul class="flex">
          <li :class="['curPer',{'active':item.id==legalId}]" v-for="item in legals" :key="item.id" @click="tabSelect(item.id,item.name)">{{item.name}}</li>
        </ul>
      </div>

      <div class="list-box">
        <div class="list-title flex ft14 gray9 between">
          <div class="tl width2">{{$t('fat.shoper')}}</div>
          <div class="width1 tc">{{$t('td.num')}}</div>
          <div class="width2 tc">{{$t('td.limit')}}</div>
          <div class="width1 tc">{{$t('td.price')}}</div>
          <div class="width1 tc">{{$t('td.method')}}</div>
          <div class="tr width1">{{$t('td.do')}}</div>
        </div>
        <ul class="list" v-if="list.length > 0">
          <li
            v-for="(item,index) in list"
            :key="index"
            class="bdb flex bod_bc ft14 tc between"
            v-if="item.is_shelves == 1"
          >
            <div class="flex alcenter width2">
              <div
                class="head"
                :style="{background:item.bgcolor}"
              >{{item.seller_name || '' | strFirst}}</div>
              <div class="flex column">
                <span class="light_blue bold">{{item.seller_name}}</span>
              </div>
            </div>
            <div
              class="flex alcenter tc width1 center"
            >{{item.surplus_number || '0.000' | numFilters(3)}} {{item.currency_name}}</div>
            <div
              class="flex alcenter tc width2 center"
            >{{item.min_number || '0.000' | numFilters(3)}}{{item.currency_name}}-{{item.max_number || '0.000' | numFilters(3)}}{{item.currency_name}}</div>
            <div
              class="flex alcenter price ft16 bold center width1 tc"
            >{{item.price || '0.000' | numFilters(3)}}</div>
            <!-- <div class="flex alcenter">{{item.way_name}}</div> -->
            <div class="flex alcenter center width1">
              <img v-if="item.way == 'ali_pay'" src="../assets/images/zfb_icon.png">
              <img v-if="item.way == 'we_chat'" src="../assets/images/wx_icon.png">
              <img v-if="item.way == 'bank'" src="../assets/images/bank_icon.png">
            </div>
            <div
              class="flex alcenter end width1 tr"
              @click="buySell(item.price,item.limitation.min,item.limitation.max,item.id,item.type,item.surplus_number,item.min_number,item.max_number)"
            >
              <button class="curPer buy-btn">{{classify}}{{item.currency_name}}</button>
            </div>
          </li>
        </ul>
        <!-- 分页 -->
        <el-pagination
          layout="prev, pager, next"
          v-if="total > 10"
          :total="total"
          @current-change="handleCurrentChange"
        ></el-pagination>
      </div>
      <!-- 出售购买弹窗 -->
      <el-dialog title :visible.sync="dialogVisible" width="600px" center>
        <div class="transfer-content">
          <h3>{{classify}}{{legalName}}</h3>
          <div class="transfer-list">
            <div class="flex">
              <p class="transfer-left">{{$t('td.price')}}{{prices || '0.000' | numFilters(3)}}</p>
            </div>
            <div class="trade">
              <p
                :class="['trade-num',{'active':types == 'num'}]"
                @click="tabClassify(2)"
              >{{classify}}{{$t('td.num')}}</p>
              <p :class="['trade-name',{'active':types == 'trade'}]" @click="tabClassify(1)">CNY{{$t('td.trade')}}</p>
            </div>
            <div class="totals-num flex between">
              <input
                v-if=" types == 'trade' "
                class="number"
                type="number"
                :placeholder="pala1"
                v-model="nums"
              >
              <input v-else class="number" type="number" :placeholder="pala2" v-model="nums">
              <div>
                <span class="name">{{name01}}</span>
                <button
                  class="all curPer"
                  type="button"
                  v-if=" type== 'sell' "
                  @click="allMoney();"
                >{{$t('td.allbuy')}}</button>
                <button class="all curPer" type="button" v-else @click="allMoney();">{{$t('td.allsell')}}</button>
              </div>
            </div>
            <div
              class="maxnum"
              v-if="types=='trade'"
            >{{$t('td.limit')}}：￥{{minNum || '0.000' | numFilters(3)}}-{{maxNum || '0.000' | numFilters(3)}}</div>
            <div
              class="maxnum"
              v-if="types=='num'"
            >{{$t('td.limit')}}：{{minNumber || '0.000' | numFilters(3)}}{{name01}}-{{maxNumber || '0.000' | numFilters(3)}}{{name01}}</div>
            <div class="trade-totals">
              <p class="total-price">{{$t('td.tradeTotal')}}</p>
              <p class="prices" v-if=" types == 'trade' ">￥{{nums || '0.000' | numFilters(3)}}</p>
              <p class="prices" v-else>￥{{nums * prices || '0.000' | numFilters(3)}}</p>
            </div>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary">{{time}}{{$t('td.doceil')}}</el-button>
          <el-button type="primary" @click="buyOrder">{{$t('cuy.confirm')}}</el-button>
        </span>
      </el-dialog>
    </div>
    <!-- <indexFooter></indexFooter> -->
  </div>
</template>

<script>
// import Paginate from 'vuejs-paginate';
import indexHeader from "@/view/indexHeader";
import indexFooter from "@/view/indexFooter";
export default {
  // components: {
  // 	Paginate
  // },
  components: { indexHeader, indexFooter },
  data() {
    return {
      legals: [],
      list: [],
      type: "sell",
      id: 0,
      page: 1,
      classify: '',
      name: "",
      pages: 0,
      prices: 0,
      minNum: 0,
      maxNum: 0,
      minNumber: 0,
      maxNumber: 0,
      max: 0,
      names: "",
      time: "60",
      shows: false,
      types: "num",
      nums: "",
      surplus_number: 0,
      totalNums: "0.00",
      ID: "",
      money_type: "",
      name01: "",
      more: true,
      surplus_number: 0,
      user_legal_balance: 0,
      bgcolor: [
        "#5d8cc2",
        "#6d78a8",
        "#a2b240",
        "#61b88e",
        "#e35744",
        "#a16c92",
        "#66756e"
      ],
      interval: function() {},
      applicationShow: false,
      applicationStatus: 0,
      total: 0,
      legalId: "",
      legalName: "",
      dialogVisible: false,
      pala1: "",
      pala2: "",
      token: "",
      skins: localStorage.getItem("skin") || "days"
    };
  },
  beforeCreate() {
    if (
    localStorage.getItem("skin") &&
    localStorage.getItem("skin") == "nights"
    ) {
      document
        .querySelector("html")
        .setAttribute("style", "background-color:#000;");
    } else {
      document
        .querySelector("html")
        .setAttribute("style", "background-color:#f8f6f6;");
    }
  },
  beforeDestroy() {
    document.querySelector("html").removeAttribute("style");
  },
  created() {
    
    this.token = window.localStorage.getItem("token") || "";
    this.getCoins();
    // if (token) {
    //   this.token = token;
    //   this.getCoins();
    // }
  },
  filters: {
    strFirst: function(value) {
      return value.substr(0, 1);
    }
  },
  mounted (){
    this.classify = this.$t('td.buy');
  },
  methods: {
    close() {
      this.shows = false;
      clearInterval(this.interval); //清除定时器
      this.time = 60;
    },
    getCoins() {
      let that = this;
      this.$http({
        url: "/api/currency/list"
      }).then(res => {
        if (res.data.type == "ok") {
          var list = res.data.message.legal;
          if (list.length) {
            that.legals = list;
            var id = list[0].id;
            that.name = list[0].name;
            that.legalId = id;
            that.legalName = list[0].name;
            that.getList();
          }
        }
      });
      this.$http({
        url: "/api/user/info",
        headers: {
          Authorization: this.token
        }
      }).then(res => {
        if (res.data.type == "ok") {
          if (res.data.message.is_seller == 0) {
            that.applicationShow = true;
            that.applicationStatus = 0;
          } else {
            if (res.data.message.seller[0].status == 0) {
              that.applicationShow = true;
              that.applicationStatus = 1;
            } else {
              that.applicationShow = false;
            }
          }
        }
      });
    },
    // 切换法币
    tabSelect(ids,names){
      var that = this;
      that.page = 1;
      that.legalId = ids; 
      that.legalName = names;
      that.getList();
    },
    getList() {
      var that = this;
      var i = layer.load();
      that.list = [];
      this.$http({
        url: "/api/legal_deal_platform",
        params: {
          type: that.type,
          page: that.page,
          currency_id: that.legalId
        },
        headers: {
          Authorization: that.token
        }
      }).then(res => {
        if (res.data.type == "ok") {
          layer.close(i);
          let list = res.data.message.data;
          if (list.length > 0) {
            for (var j = 0; j < list.length; j++) {
              list[j].bgcolor = this.bgcolor[
                Math.floor(Math.random() * 6 + 1) - 1
              ];
            }
            that.list = list;
            that.total = res.data.message.total;
          }
        }
      });
    },
    // 点击改变选中分类
    changeClassify(type) {
      let that = this;
      that.list = [];
      that.type = type;
      if (type == "sell") {
        that.classify = that.$t('td.buy');
      } else {
        that.classify = that.$t('td.sell');
      }

      that.page = 1;
      that.getList();
    },
    // 出售或者购买按钮
    buySell(prices, min, max, id, type, surplus_number, minNumber, maxNumber) {
      var that = this;
      that.types = "num";
      that.name01 = that.legalName;
      clearInterval(that.interval);
      that.dialogVisible = true;
      if (type == "sell") {
        that.pala1 = that.$t('td.buytotal');
        that.pala2 = that.$t('td.buyallnum');
        that.money_type = that.$t('td.buy');
      } else if (type == "buy") {
        that.money_type = that.$t('td.sell');
        that.pala1 = that.$t('td.selltotal');
        that.pala2 = that.$t('td.sellallnum');
      }
      let _this = this;
      (that.nums = ""), (that.shows = true);
      that.ID = id;
      that.time = 60;
      document.getElementsByTagName("body")[0].className = "body";
      that.prices = prices;
      that.minNum = min;
      that.maxNum = max;
      that.surplus_number = surplus_number;
      that.minNumber = minNumber;
      that.maxNumber = maxNumber;
      that.interval = setInterval(function() {
        that.time--;
        if (that.time <= 0) {
          that.shows = false;
          document.body.removeAttribute("class", "body");
          clearInterval(that.interval); //清除定时器
          that.time = 60;
        }
      }, 1000);
      that
        .$http({
          url: "/api/legal_deal_info",
          method: "get",
          params: { id: id },
          headers: {
            Authorization: localStorage.getItem("token")
          }
        })
        .then(res => {
          if (res.data.type == "ok") {
            that.surplus_number = res.data.message.surplus_number;
            that.user_legal_balance = that.$utils.filterDecimals(
              res.data.message.user_legal_balance - 0
            ,3);
            that.minNumber = res.data.message.min_number;
            that.maxNumber = res.data.message.max_number;
          }
        });
    },
    // 交易或数量切换
    tabClassify(num) {
      this.nums = "";
      if (num == 1) {
        this.types = "trade";
        this.name01 = "CNY";
      } else {
        this.types = "num";
        this.name01 = this.legalName;
      }
    },
    // 全部卖出或买入
    allMoney() {
      if (this.type == "buy") {
        //出售
        if (this.types == "trade") {
          //cny交易
          this.nums = this.$utils.filterDecimals(this.user_legal_balance * this.prices,3);
        } else {
          this.nums = this.$utils.filterDecimals(this.user_legal_balance,3);
        }
      } else {
        if (this.types == "num") {
          this.nums = this.$utils.filterDecimals(this.surplus_number,3);
        } else {
          this.nums = this.$utils.filterDecimals(this.maxNum,3);
        }
      }
    },
    // 下单
    buyOrder() {
      let _this = this;
      let means = "money";
      let ids = window.localStorage.getItem("user_id");
      let token = window.localStorage.getItem("token") || "";
      if (_this.nums) {
        if (_this.types == "trade") {
          means = "money";
        } else {
          means = "number";
        }
        let datas = {
          id: _this.ID,
          means: means,
          value: _this.nums
        };
        _this.buyHttp("/api/do_legal_deal", datas, function(res) {
          if (res.data.type == "ok") {
            if (res.data.message.data.type == "sell") {
              layer.msg(res.data.message.msg);
              setTimeout(function() {
                _this.$router.push({
                  path: "/legalPay",
                  query: { id: res.data.message.data.id }
                });
              }, 500);
            } else {
              layer.msg(res.data.message.msg);
              setTimeout(function() {
                _this.$router.push({
                  path: "/legalPayDetail",
                  query: { id: res.data.message.data.id }
                });
              }, 500);
            }
          } else {
            layer.msg(res.data.message);
          }
        });
      } else {
        if (_this.type == "sell") {
          if (_this.types == "trade") {
            layer.msg(_this.$t('td.buytotal'));
          } else {
            layer.msg(_this.$t('td.buyallnum'));
          }
        } else {
          if (_this.types == "trade") {
            layer.msg(_this.$t('td.selltotal'));
          } else {
            layer.msg(_this.$t('td.sellallnum'));
          }
        }
      }
    },
    // 请求
    buyHttp(urls, params, callback) {
      let _this = this;
      _this
        .$http({
          url: urls,
          method: "post",
          data: params,
          headers: {
            Authorization: localStorage.getItem("token")
          }
        })
        .then(res => {
          if (res.data.type == "ok") {
            // layer.msg(res.data.message)
            callback && callback(res);
          } else {
            layer.msg(res.data.message);
            if (res.data.type == "997") {
              setTimeout(() => {
                _this.$router.push("/userSetting");
              }, 1500);
            }
            if (res.data.type == "998") {
              setTimeout(() => {
                _this.$router.push("/authentication");
              }, 1500);
            }
          }
        });
    },
    // 跳转订单记录
    recordList() {
      let _this = this;
      // _this.$router.push({ path: "/LegalRecord", query: { id: _this.id } });
      _this.$router.push({ path: "/LegalRecord"});
    },

    // 跳转申请页面
    application() {
      let that = this;
      if (that.applicationStatus == 1) {
        layer.msg(that.$t('td.examine'));
      } else {
        that.$router.push({ path: "/LegalApplication" });
      }
    }, // 分页
    handleCurrentChange(val) {
      var that = this;
      that.page = val;
      that.getList();
    }
  }
};
</script>

 <style scoped>
.buy-box {
  padding: 20px;
}
.sell-box {
  padding: 20px;
}

.white {
  width: 1200px;
  margin: 66px auto;
  min-height: 650px;
  background-color: #fff;
  -webkit-box-shadow: 0 0 29px 0 rgba(32, 36, 48, 0.08);
  box-shadow: 0 0 29px 0 rgba(32, 36, 48, 0.08);
  padding-bottom: 30px;
  position: relative;
  top:18px;
}
.buy-sell {
  position: relative;
}

.now li {
  color: #f0b90b;
}
.current {
  border-bottom: 2px solid #f0b90b;
  font-weight: 600;
  color: #f0b90b;
  padding: 2px 6px;
}
.record {
  cursor: pointer;
  font-size: 16px;
  position: absolute;
  right: 20px;
  text-align: right;
  top: 30px;
  width: 100px;
}
.application {
  cursor: pointer;
  font-size: 16px;
  position: absolute;
  right: 100px;
  text-align: right;
  top: 30px;
}
.list-box {
  margin-top: 20px;
}
.list-title {
  padding: 10px 20px;
  border-bottom: 1px solid #e6ecf2;
}
.list {
  padding: 0 20px 10px;
}
.width1 {
  width: 17%;
}
.width2 {
  width: 24%;
}
.list li {
  padding: 20px 0;
}
.price {
  color: #489972;
}
.head {
  width: 36px;
  height: 36px;
  line-height: 36px;
  border-radius: 50%;
  margin-right: 10px;
  color: #fff;
}
span {
  display: inline-block;
  position: relative;
}

.list li div:last-child {
  text-align: right;
  justify-content: flex-end;
}
.all {
  background-color: transparent;
  font-size: 15px;
  line-height: 20px;
  margin-right: 15px;
  margin-top: 9px;
  padding-left: 20px;
  border: none;
  border-left: 1px solid #cad7e0;
}
/* button {
  background-color: #f0b90b;
  padding: 0px 10px;
  color: #fff;
  border-radius: 4px;
  line-height: 36px;
  border: none;
} */
.pages {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
li {
  padding: 10px;
  /* border-radius: 6px; */
}
.modal {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
}
.mask {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #000;
  opacity: 0.3;
  overflow: hidden;
}
.content {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 100;
  overflow: hidden;
}
.content-list {
  width: 400px;
  border-radius: 10px;
  margin: auto;
  background-color: #fff;
  padding: 15px;
}
.title {
  font-size: 16px;
  line-height: 2;
  text-align: center;
  margin-right: 15px;
  cursor: pointer;
}
.trade {
  font-size: 0;
}
p {
  display: inline-block;
  font-size: 15px;
  margin: 15px;
  margin: 10px 10px 10px 0;
  line-height: 1.5;
  padding-bottom: 5px;
  cursor: pointer;
}
.active {
  border-bottom: 2px solid #f0b90b;
}
.totals-num {
  width: 100%;
  height: 40px;
