  border-radius: 4px;
  padding: 0 10px;
}
span {
  font-size: 15px;
  line-height: 38px;
  margin-right: 20px;
}
.maxnum {
  margin-top: 15px;
}
.trade-totals {
  width: 100%;
  font-size: 0;
  height: 40px;
