}
.total-price {
  float: left;
  font-size: 15px;
}
.prices {
  font-size: 15px;
  float: right;
}
.tip {
  font-size: 14px;
}
.btns {
  margin-top: 10px;
  font-size: 0;
  height: 40px;
}
.cannel {
  width: 48%;
  height: 40px;
