}
.balck{
  background-color: #1e2b34;
  color: #fff;
}
.balck .list-box .list-title {
  padding: 10px 20px;
  border-bottom: 1px solid #252323;
}
.balck .bdb{
  border-bottom: 1px solid #252323;
}
</style>



